@use '_colors/light' as *;
@use '_colors/dark' as *;

@media (prefers-color-scheme: light) {
    body {
        @include light-mode-body;
    }
    @include light-mode;
}

@media (prefers-color-scheme: dark) {
    body {
        @include dark-mode-body;
    }
    @include dark-mode;
}